<template>
  <div class="home">
    <div class="content">
        <div class="login-form">
          <div class="project-name">{{$global.PROJECT_NAME}}</div>
          <el-form :model="editForm" status-icon :rules="rules" ref="editForm" label-width="100px">
            <div class="login-input">
              <el-input v-model="editForm.loginName" placeholder="请输入登录名"></el-input>
            </div>
            <div class="login-input">
              <el-input type="password" v-model="editForm.password" placeholder="请输入密码" autocomplete="off"></el-input>
            </div>
            <div class="login-input">
                <span v-if="true" class="register-span">
                    <small>没有账号?&nbsp;&nbsp;</small><a class="register-a" onclick="register()">立即注册</a>
                </span>
              <el-button style="width: 100%" type="primary" @click="login">登录</el-button>
            </div>
          </el-form>
        </div>
  </div>
  </div>
</template>
<script>
  import {mapActions} from "vuex";

  export default {
    data() {
      return {
        editForm: {
          loginName: '',
          password: '',
        },
        rules: {
          loginName: [
            {required: true, message: '请输入登录名', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'}
          ]
        }
      };
    },

    methods: {
      ...mapActions(['commitConfig', 'commitDictList', 'commitNoticeList', 'commitToken', 'commitLoginInfo']),
      initData() {
        const loading = this.$loading({
          lock: true,
          text: '请稍后,登录中...',
          spinner: 'el-icon-loading',
          background: 'rgba(0,0,0,0.25)'
        });
        //加载配置数据
        this.commitConfig().then(res => {
          //加载数据字典
          this.commitDictList().then(res => {
            //获取登录信息
            this.commitLoginInfo().then(res => {
              let getters = this.$store.getters;
              console.log("getters:",getters)
              this.goHomePage();
              loading.close();
            })
          })
        })
      },
      goHomePage() {
        this.$router.push('/main');
      },
      login() {
        this.commitToken().then(res => {
          this.submitForm('editForm');
        })
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {//验证通过
            this.$request.doPost("/api/login/userLogin", this.editForm).then(res => {
              if (res.isOk) {
                this.initData();//初始化数据
                this.showSuccess(res.info);
              } else {
                this.showWarn(res.info);
              }
            });
          }
        });
      },
      // resetForm(formName) {
      //   this.$refs[formName].resetFields();
      // }
    }
  }
</script>
<style scoped>
  .home{
    width: 100%;
    height: 100vh;
    background: url("/static/login/images/login.jpg") center center no-repeat;
    background-size: 100% 100%;
  }
  .content {
    width: 350px;
    margin: 0 auto; /*水平居中*/
    position: relative;
    top: 50%; /*偏移*/
    transform: translateY(-50%);
  }
  .title{
    padding: 20px;
    text-align: center;
    color: #6a6c6f;
  }
  .login-form{
    border-radius: 10px;
    width: 350px;
    height: 320px;
    background: rgba(255, 255, 255, 0.85);
  }
  .login-input{
    margin: 25px;
  }
  .register-span{
    display: block;
    padding-right: 3px;
    padding-bottom: 5px;
    float: right
  }
  .register-a{
    cursor: pointer;
  }

  .project-name{
    padding:10px;
    font-size: 30px;
    text-shadow: 2px 3px 1px black;
    color: white;
    font-weight: bold;
    text-align: center;
    margin-bottom: 10px;
    /*margin: 0 auto; !*水平居中*!*/
  }
</style>



